.footer {
	// background-color: #f8f8f8;
	background: url('../images/logo-bg2.svg') no-repeat #f8f8f8;
	background-size: auto;
	background-position: center center;
	padding: 32px 0 19px;
	z-index: 3;
	position: relative;
	min-width: 1176px;



	.container {
		padding: 0;
	}

	&__inner {
		display: flex;
		flex-wrap: wrap;
	}

