	// 			width: 100%;
	// 		}

	// 		.menu-footer__item {
	// 			width: 50%;
	// 		}

	// 		.menu-footer__item {
	// 			width: 50%;
	// 		}
	// 	}

	// 	&:nth-child(4) {
	// 		width: 15%;
	// 		flex-basis: 180px;
	// 		display: flex;
	// 		flex-direction: column;
	// 		flex-basis: 100px;
	// 		padding-top: 12px;
	// 	}
	// }
	&__col {
		width: 25%;
		flex-basis: 220px;
		flex-grow: 1;

		.menu-footer__item {
			width: 85%;
			cursor: pointer;
			font-family: Arial;
			font-size: 13px;
			line-height: 16px;
			color: $base;
			margin-bottom: 8px;
		}

		&:nth-child(2) {
			.menu-footer__title {
				margin-bottom: 0px;

				&+.menu-footer__title {
					line-height: 24px;
					margin-bottom: 10px;
				}
			}
		}

		&:nth-child(3),
		&:nth-child(4) {
			width: 39%;
			flex-basis: 310px;


			.menu-footer__list {
				display: flex;
				flex-wrap: wrap;
				height: 181px;
				align-content: baseline;
			}

			.menu-footer__title {
				width: 100%;
			}

			.menu-footer__item {
				width: 50%;
			}

			.menu-footer__item {
				width: 50%;
			}
		}

		// &:nth-child(4) {
		// 	width: 15%;
		// 	flex-basis: 180px;
		// 	display: flex;
		// 	flex-direction: column;
		// 	flex-basis: 100px;
		// 	padding-top: 12px;
		// }
		&:last-child {
			width: 15%;
			flex-basis: 192px;
			display: flex;
			flex-direction: column;
			flex-grow: 0;
			padding-top: 12px;
		}
	}

	&__row {
		width: 100%;
		display: flex;
		padding-top: 34px;
		align-items: center;
		flex: 1 1 100%;
		justify-content: space-between;
	}

	&__copyright {
		font-weight: normal;
		font-style: normal;
		font-stretch: normal;
		letter-spacing: normal;
		text-align: left;
		color: #666666;
		font-family: Arial;
		font-size: 13px;
		line-height: 20px;

		br {
			display: none;
		}
	}

	&__button {
		height: 34px;
		padding: 4px 14px 4px 14px;
		border-radius: 3px;
		background-color: $base;
		color: #ffffff;
		font-family: "Roboto Condensed";
		font-size: 14px;
		font-weight: 400;
		color: #ffffff;
		display: flex;
		align-items: center;
	}

	&__text {
		color: #666666;
		font-family: Arial;
		font-size: 13px;
		line-height: 22px;
		text-align: right;
		b {
			font-weight: bold;
		}
	}

	&__tel {
		color: #333333;
		font-size: 20px;
		font-weight: 400;
		line-height: 26px;
		text-align: right;
		text-decoration: none !important;
	}

	&__email {
		color: $base;
		font-family: Arial;
		font-size: 13px;
		line-height: 22px;
		text-align: right;
		margin: 7px 0 6px 0;
		text-decoration: none !important;

		i {
			color: #666666;
			font-style: normal;
		}
	}

	&__telephones {
		display: flex;
		min-width: 195px;
		justify-content: flex-end;
	}

	.menu-footer {
		&__title {
			font-size: 18px;
			font-weight: 400;
			line-height: 36px;
			color: $base;
			font-family: "Roboto Condensed";
			// cursor: pointer;
			margin-bottom: 10px;

		}

		&__link {
			color: #666666;
			font-family: Arial;
			font-size: 13px;
			line-height: 16px;
			text-decoration: none !important;

			&:hover {
				color: $base;
			}
		}

		&__list {
			margin: 0;
			padding: 0;
			list-style-type: none;

		}
	}

	.social-items {
		display: flex;
		// margin-left: 8%;

		.social-link {
			width: 20px;
			height: 20px;
			margin-right: 25px;

			&:last-child {
				margin-right: 0;
			}

			&.vk {
				background: url('../images/social-items-vk.png') no-repeat #f8f8f8;
			}

			&.fb {
				background: url('../images/social-items-facebook.png') no-repeat #f8f8f8;
			}

			&.tw {
				background: url('../images/social-items-instagram.png') no-repeat #f8f8f8;
			}

			&.ok {
				background: url('../images/social-items-ok.png') no-repeat #f8f8f8;
			}
		}


	}

	.privacy-policy {
		color: #666666;
		font-family: Arial;
		font-size: 13px;
		line-height: 20px;
		text-decoration: none !important;

		&:hover {
			color: $base;
		}
	}

	.footer__call-phone-button {
		color: $base;
		font-family: Arial;
		font-size: 13px;
		line-height: 22px;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		margin: 10px 0 0 0;
		text-decoration: none !important;

		&::before {
			content: '';
			background: url('../images/footer__call.png');
			// background: url('../images/social-items-vk.png') no-repeat #f8f8f8;
			margin-right: 10px;
			width: 12px;
			height: 12px;
		}

		span {
			border-bottom: 1px dashed #394791;
		}
	}

	.abuse__button {
		text-decoration: none !important;
	}




	@media screen and (max-width: 767px) {
		padding: 0 0 19px;
		background: #f8f8f8;
		min-width: 320px;
		width: auto;
		max-width: 100%;


		* {
			transition: 0.3s;
		}

		&__row {
			flex-wrap: wrap;
			padding-top: 16px;
			flex-direction: column;
			align-items: baseline;
		}

		&__copyright {
			width: 100%;
			text-align: left;
			margin-bottom: 16px;

			@media screen and (max-width: 443px) {
				br {
					display: block;
				}
			}
		}

		&__telephones {
			margin: 0px;
			width: 100%;
			order: -1;
			margin-bottom: 12px;

			.l-abuse,
			.footer__button {
				width: 100%;
			}
		}

		&__button {
			width: 288px;
			height: 32px;
			text-align: center;
			justify-content: center;
		}

		.footer {
			&__col {
				flex-basis: unset;
				flex-grow: 0;
				margin-bottom: 0;
				width: 100% !important;
				margin-bottom: 0 !important;
				border-bottom: 1px solid #eeeeee;
				margin-left: 0 !important;
				display: flex;

				.menu-footer__item {
					width: 100%;
					max-width: 100%;
				}

				&.hide {
					display: flex !important;
				}

				&:nth-child(1) {
					.menu-footer {
						background: url("../images/grohe.svg") no-repeat 0 10px;
					}
				}

				&:nth-child(2) {
					.menu-footer {
						background: url("../images/sfera.svg") no-repeat 0 10px;

						&__title {
							display: none;

							&.js-menu-footer__title {
								display: list-item;
							}
						}
					}
				}

				&:nth-child(3) {
					.menu-footer {
						background: url("../images/collections.svg") no-repeat -1px 14px;
					}
				}

				&:nth-child(4) {
					border: 0;
					margin: -2px 0 -1px;

					.menu-footer {
						// background: url("../images/service.svg") no-repeat 0 10px;
						padding: 0;
						height: auto;
						overflow: visible;
					}

					// padding-top: 0px !important;

					.menu-footer__list {
						flex-direction: row;
						flex-wrap: wrap;
						display: flex;
						padding: 7px 16px 7px;
						width: calc(100% + 32px);
						background-color: #f1f1f1;
						margin: 0 -16px;
					}

					.menu-footer__item {
						width: 50%;
						margin: 8px 0;
						cursor: pointer;

						// &:last-of-type {
						// 	width: 100%;
						// }
					}

					.menu-footer__title {
						display: none;
					}

					.menu-footer__link {
						&:before {
							display: none;
						}
					}
				}

				&:last-child {
					// flex-direction: row;
					position: relative;
					flex-wrap: wrap;
					max-height: 165px;
					padding-top: 40px;

					.footer__text,
					.footer__tel,
					a {
						width: 50%;
					}

					.footer__text_first {
						margin-top: 0px;
						width: 100%;
						position: absolute;
						top: 12px;
					}

					.footer__email {
						order: 3;
					}

					.footer__call-phone-button {
						margin-bottom: 16px;
						margin-top: 20px;
					}

					.footer__text_two {
						margin-top: 0px;
					}
				}
			}

		}

		.menu-footer {
			height: 51px;
			overflow: hidden;
			display: flex;
			margin-left: 0 !important;
			flex-wrap: wrap;
			padding-left: 31px;
			border-bottom: 1px solid #f1f1f1;
			width: 100%;

			&__title {
				line-height: 27px;
				font-size: 16px;
				margin-bottom: 0px;
				padding: 12px 16px 12px 0;
				background: url("https://icongr.am/entypo/chevron-down.svg?color=666666") 100% 50%/16px no-repeat;
				color: #333333;
				cursor: pointer;
				width: 100%;

				a {
					color: inherit;
					text-decoration: none;
				}
			}

			&__item {
				padding: 0;
			}

			&__list {
				display: block;
				height: unset !important;
				padding-bottom: 17px;
				width: 100%;
			}

			&__link {

				&:before {
					content: '∙ ';
					// position: absolute;
					// left: 0;
				}
			}

			&__dropdown {
				width: 100% !important;
				max-width: 100% !important;
				margin-bottom: 0;
				border-bottom: 1px solid #f1f1f1;

				.menu-footer__subtitle {
					padding: 8px 17px 8px;
					width: 100%;
					line-height: 22px;
					margin-bottom: 0 !important;
					background: url("https://icongr.am/entypo/chevron-right.svg?color=b0b0b0") calc(100% - 14px) 50%/18px no-repeat;
				}

				.sub-menu__list {
					display: none;
				}

				.sub-menu__item {
					color: #333333;
					font-family: "Roboto Condensed";
					font-size: 14px;
					font-weight: 400;
					line-height: 29px;
					white-space: nowrap;
					padding-left: 11px;
					line-height: 23px;
					margin-bottom: 6px;

					.sub-menu__link {
						color: #666;
						font-size: 13px;
						font-weight: 400;
					}


					&:before {
						content: '∙ ';
						// position: absolute;
						// left: 0;
					}
				}

				&.active {
					.menu-footer__subtitle {
						background: url("https://icongr.am/entypo/chevron-down.svg?color=b0b0b0") calc(100% - 14px) 50%/18px no-repeat;
					}

					.sub-menu__list {
						display: flex;
						padding-left: 13px;
						margin-bottom: 20px;
						max-height: unset !important;
						flex-direction: column;
						padding-left: 25px;
						list-style: none;
					}
				}
			}

		}

		.active {
			height: unset !important;
			overflow: unset !important;

			.menu-footer__title {
				background: url("https://icongr.am/entypo/chevron-up.svg?color=394791") 100% 50%/16px no-repeat;
				color: $base;
			}
		}

		.checkbox__oferta {
			color: #666666;
			font-family: Arial;
			font-size: 12px;
			line-height: 18px;
		}

		.footer__text {
			width: unset;
			margin-top: 4%;
			text-align: left;

			&_three {
				margin-top: 0;
			}

		}

		.footer__tel {
			text-align: left;
		}

		.footer__call-phone-button {
			justify-content: flex-start;
		}

		.footer__email {
			text-align: left;


		}

		.social-items {
			margin-bottom: 12px;
		}

		.privacy-policy {
			margin-bottom: 12px;
		}
	}
}


.abuse-popup-container {
	// height: 100vh;
	// overflow-y: scroll;
	-webkit-overflow-scrolling: touch;
}
